import React, { useState, useEffect, ChangeEvent } from "react";

import styles from "./Profile.module.css";
import edit from "../../img/icon/edit.png";

import { compose } from "redux";
import { connect } from "react-redux";
import { globalStateType } from "../../redux/redux-store";
import { updateStatus } from '../../redux/profileReducer'

type MapStateType = {
  isAuth: boolean,
  errorStatus: string,
  status: string
}
type MapDispatchType = {
  updateStatus: (status: string) => void 
}
type OwnPropsType = {
  isOwner: boolean
}
type PropsType = MapStateType & MapDispatchType & OwnPropsType

// useState  
// type StateType = {
//   status: string
//   editMode: boolean
// }

const ProfileStatus: React.FC<PropsType> = ({status, isAuth, isOwner, errorStatus, updateStatus }) => {
  const [statusUS, setStatus] = useState(status);
  const [editMode, setEditMode] = useState(false);
    
  useEffect(() => {
    setStatus(status);
  }, [status]);

  const onEditMode = () =>  {
    if(isAuth && isOwner) setEditMode(true);
  };
  const offEditMode = () => {
    setEditMode(false);
    updateStatus(statusUS);
  };
  const onStatusChange = (e: ChangeEvent<HTMLInputElement>) => {
    setStatus(e.currentTarget.value);
  };
  return (
    <div className={styles.wrapperBodyStatus}>
      {!editMode && (        
        <div className={styles.wrapperTextStatus}>
          <span onClick={ onEditMode }
                className={styles.textStatus}>
            {status || ""}
            {(isAuth && isOwner && !!statusUS && <img src={edit} alt="" />) ||
             (isOwner && isAuth && " ")}
            {errorStatus && <span className={styles.errorStatus}>{errorStatus}</span>}
          </span>
        </div>
      )}
      {editMode && (
        <input autoFocus onBlur={offEditMode} onChange={onStatusChange} value={statusUS} />
      )}
    </div>
  );
};
const mapStateToProps = (state: globalStateType) => {
  return {
    isAuth: state.auth.isAuth,
    status: state.profilePage.status,
    errorStatus: state.profilePage.messageError.errorStatus,
  };
};
export default compose(connect(mapStateToProps, {updateStatus}), 
)(ProfileStatus);
